.photogallery-container {
  width: 100%;
  height: auto;
  background-color: rgb(138, 115, 241);
  padding: 15px;
  display: flex;
  flex-direction: column;
}

.photogallery-title {
  padding-top: 15px;
  text-align: center;
  color: #fcf1ce;
}

.photogallery-images {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 20px;
  gap: 20px;
  padding: 15px;
}

.photogallery-images img {
  width: 100%;
  max-width: 400px;
  max-height: 300px;
  display: block;
  border-radius: 5px;
  transition: all 100ms ease-out;
}

.photogallery-images img:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  transition: all ease 0.5s;
}

.photogallery-container button {
  font-size: 18px;
  font-weight: 800;
  padding: 8px 5px;
  border-radius: 5px;
  outline: none;
  align-items: left;
  margin-left: 12px;
  width: 160px;
  color: white;
  border: 1px solid white;
  background-color: rgb(233, 83, 75);
}

.photogallery-container button:hover {
  background-color: white;
  color: rgb(233, 83, 75);
  border: 1px solid rgb(233, 83, 75);
  transition: all ease 0.5s;
}

.popularbooks-container{
    background-color: rgb(247,235,215);
    display: flex;
    flex-direction: column;
}

.popularbooks{
    padding: 20px;
    overflow: hidden;
    width: 100%;
    margin: 0 auto;	
    position: relative;
	display: flex;
    height: auto;
}

.popularbooks:hover div{
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}

.popularbooks-title{
    position: relative;
    color: #0E2431;
    margin-top: 20px;
    margin-bottom: 50px;
    text-align: center;
    font-size: 32px;
}

.popular-book:hover{
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
    transition: all ease 0.7s;
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}

.popularbook-images{
    -webkit-animation: marquee 15s linear infinite;
            animation: marquee 15s linear infinite;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
}

.popular-book{
    display: block;
    width: 150px;
    height: auto;
    margin: 0 1vw;
    border-radius: 5px;
}

@-webkit-keyframes marquee {
	0%   { -webkit-transform: translateX(0%); transform: translateX(0%); }
	100% { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
}

@keyframes marquee {
	0%   { -webkit-transform: translateX(0%); transform: translateX(0%); }
	100% { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
}
