.activebooks-table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
  margin-bottom: 10px !important;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.activebooks-table td,
.activebooks-table th {
  border: 1px solid #ddd;
  padding: 8px;
  font-size: 16px;
}

.activebooks-table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: rgb(24, 33, 62);
  color: white;
}

@media screen and (max-width: 768px) {
  .user-details-topbar {
    flex-direction: column;
  }
  .user-details-specific {
    flex-direction: column;
  }
  .specific-left,
  .specific-right {
    width: 100%;
    margin: 20px auto;
  }
}

.books-page{
    height: 100vh;
    width: 100vw;
    background-color: wheat;
}

.books{
    margin: 60px auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
    gap: 20px;
    padding: 15px;
    max-width: 1024px;
}

.book-card{
    display: flex;
    flex-direction: column;
    box-sizing: content-box;
    height: auto;
    width: 250px;
    align-items: center;
    padding: 25px;
    margin: 10px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    position: relative;
    background-color: rgb(238, 238, 238);
}

.book-card img{
    width: 150px;
    height: auto;
    margin-bottom: 25px!important;
    border-radius: 18px;
}

.book-card img:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: all ease 1.3s;
}

.bookcard-title{
    word-wrap: break-word;
    text-align: center;
    font-weight: 600;
    font-size: 16px;
}

.bookcard-author{
    color: rgb(141, 140, 140);
    font-size: 14px;
    font-weight: 900;
}

.bookcard-category{
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    bottom: 5%;
    left: 5%;
}

.bookcard-category p{
    padding: 8px 15px;
    background-color: lightgray;
    font-weight: 600;
    margin: 5px;
    border-radius: 5px;
}

.bookcard-emptybox{
    height: 50px;
    position: relative;
    bottom: 0%;
    background-color: rgb(0, 0, 0);
}
/*Desktop View*/
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 40px;
    height: 60px;
    width: 100%;
    position: fixed;
    z-index: 100;
    background-color: rgb(16,25,13);
    box-shadow: 0 1px 4px rgba(146,161,176,.15);
    top: 0%;
}

.logo-nav a{
    font-size: 20px;
    color: rgb(233,83,75);
    font-weight: 800;
}

.logo-nav a:hover{
    color: wheat;
    transition: all ease 0.5s;
}

.search-input{
    width: 100%;
    margin-left: 15px;
    max-width: 1800px;
    height: 40px;
    outline: none;
    padding: 10px 15px;
    font-size: 18px;
    border-radius: 10px;
    border: 1px solid white;
}

.nav-options{
    padding-left: 25px;
    display: flex;
    list-style-type: none;
    margin-bottom: 15px!important;
}

.nav-right{
    display: flex;
    align-items: center;
    justify-content: center;
}
  
.option{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 40px;
    margin-top: 15px;
    color: rgb(243, 6, 6);
}

.option a{
    color: rgb(233,83,75);
    display: flex;
    align-items: center;
    font-weight: 800;
    font-size: 18px;
}

.option a:hover{
    color: wheat;
    transition: all ease 0.5s;
}

a{
    text-decoration: none!important;
}
  
.option:hover{
    color: rgb(6, 240, 170);
}

.mobile-menu{
    display: block;
}

/* Nav Options are given in this to make the search input as big as possible */

@media (min-width: 768px){
    .mobile-menu{
        display: none!important;
    }
    .header{
        display: grid;
        grid-template-columns: 10% 60%;
    }
}

@media (max-width: 768px){
    /*Mobile View */
    .header{
        padding: 0px 10px;
    }
    .nav-options{
        display: flex;
        width: 100%;
        height: 280px;
        position: absolute;
        top: 60px;
        left: -100%;
        opacity: 0;
        transition: all 0.5s ease;
        flex-direction: column;
        list-style-type: none;
        grid-gap: 0px;
    }
    .nav-options.active{
        background: black;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 100;
        align-content: center;
        padding-left: 0px;
    }
  
    .menu-icon{
        width: 45px;
        height: 45px;
        color: rgb(233,83,75);
        padding: 2px;
        background-color: rgb(246, 233, 202);
        border-radius: 5px;
        cursor: pointer;
        margin:0px 5px 0px 10px;
    }
    .option{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 10vw;
        padding: 30px 0px;
    }
}  
